

export function validationLoginLoginPage(value:string) {
    let error;
    if (value.length > 40) {
      error = 'Максимальная длина поля 40 символов';
    }
    if (value && value.length < 10) {
      error = 'Минимальная длина поля 10 символов';
    }
    return error;
  }

  export function validationPasswordLoginPage(value:string) {
    let error;
    if (value.length > 40) {
      error = 'Максимальная длина поля 40 символов';
    }
    if (value && value.length < 10) {
      error = 'Минимальная длина поля 10 символов';
    }
    return error;
  }