import { Tooltip } from "antd"
import { Field, Form, Formik } from "formik"
import { useDispatch, useSelector } from "react-redux"
import { RegistrationThunk } from "../../Redux/authReducer"
import { getAuthReducer } from "../../Selectors"
import { validationLoginLoginPage, validationPasswordLoginPage } from "../../validation"
import { valuesRegistation } from "./Main"
import 'antd/dist/antd.css'

const Registration = ()=>{


    const dispatch = useDispatch()

    const auth = useSelector(getAuthReducer)
    
    const registration = (values:valuesRegistation, a:any)=>{
      dispatch(RegistrationThunk(values)).then(()=> auth.success && a.resetForm())

    }

    return(
        <div className='auth'>
        <div className='auth_title'>Sign up</div>
        <Formik initialValues={{login: '',password: '',repeatPassword:''}}
        onSubmit={(values: valuesRegistation, a: any) => {registration(values, a)}}>
           {({ errors, touched, isValidating }) => (
        <Form>
           <Tooltip visible={errors.login && touched.login ? true : false}  placement="right" title={ <span>{errors.login}</span>}><Field id="login" name="login" placeholder="login" validate={validationLoginLoginPage}/></Tooltip>
          <Tooltip visible={errors.password && touched.password ? true : false} placement="left" title={<span>{errors.password}</span>}><Field id="password" name="password" placeholder="password" validate={validationPasswordLoginPage}/></Tooltip>

          <Field id="repeatPassword" name="repeatPassword" placeholder="repeat password" />

          <button type="submit">Submit</button>
        </Form>)}
      </Formik>
      {<div className="error"><span>{auth.error}</span></div>} {<div className="success"><span>{auth.success}</span></div>}
        </div>
    )
}

export default Registration